import { Popup } from "@gdk/core-pixi";
import type { mesh } from "pixi.js";
import type { Button, TextField } from "@gdk/core-pixi";

import { EVisualIdentityEvent } from "../../../events";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class QuitPopin extends Popup {
	/**
	 * Fill nine slice used to draw popup's border (from editor)
	 */
	public fill: mesh.NineSlicePlane = null;
	/**
	 * Title's textfield (from editor)
	 */
	public title: TextField = null;
	/**
	 * Title's textfield (from editor)
	 */
	public body: TextField = null;
	/**
	 * Quit button used to confirm quitting the game (from the editor)
	 */
	public confirmQuitButton: Button = null;

	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference.
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * Called by the project loader once the container has
	 * been imported
	 */
	public onLoaded(): void {
		// Force unvisible
		this.visible = false;

		// Border always first
		this.addChildAt(this.fill, 0);

		// Buttons names
		this.closeButton.setName("quitPopin:close");

		// Bind events
		this._bindEvents();
	}

	/**
	 * Shows the quit popin
	 */
	public show(): void {
		this.visible = true;
	}

	/**
	 * Hides the quit popin
	 */
	public hide(): void {
		this.visible = false;
	}

	/**
	 * Binds the events related to the buttons
	 */
	private _bindEvents(): void {
		// Hide
		this.closeButton.on("pointertap", () => {
			this.hide();
			this.emit(EVisualIdentityEvent.HideSettings);
		});

		// Quit
		this.confirmQuitButton.on("pointertap", () => {
			this.vi.emitter.emit(EVisualIdentityEvent.QuitGame);
			this.emit(EVisualIdentityEvent.HideSettings);
			this.visible = false;
		});
	}
}
