import i18n from "i18next";
import { EOrientation } from "@gdk/core";
import { LoadedView } from "@gdk/core-pixi";
import type { TextField } from "@gdk/core-pixi";
import type { Container } from "pixi.js";

export class LockView extends LoadedView {
	// Public members
	public portrait: Container = null;
	public landscape: Container = null;

	public text: TextField = null;

	/**
	 * Constructor
	 */
	public constructor() {
		super();
	}

	/**
	 * Sets the orientation of the game and then what message to show
	 */
	public set orientation(orientation: EOrientation) {
		if (orientation === EOrientation.Landscape) {
			// Landscape
			this.landscape.visible = true;
			this.portrait.visible = false;

			this.text.text = i18n.t("vi.orientationHint", {
				replace: {
					orientation: i18n.t("vi.orientation.landscape"),
					device: i18n.t("vi.device.mobile"),
				},
			});
		} else if (orientation === EOrientation.Portrait) {
			// Portrait
			this.landscape.visible = false;
			this.portrait.visible = true;

			this.text.text = i18n.t("vi.orientationHint", {
				replace: {
					orientation: i18n.t("vi.orientation.portrait"),
					device: i18n.t("vi.device.mobile"),
				},
			});
		}
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Force visible
		this.visible = true;
	}
}
