import { Graphics, Transform, Texture } from "pixi.js";
import i18n from "i18next";
import { tracker, SettingsEvent, ESettingsEvent, Tools } from "@gdk/core";
import { LoadedView } from "@gdk/core-pixi";
import type { Button, IStyles, TextField } from "@gdk/core-pixi";
import type { Sprite } from "pixi.js";

import { Time } from "../components/time";
import { SettingsComponent } from "../components/settingsComponent";
import { QuitPopin } from "../components/quitPopin";
import { EVisualIdentityEvent, EVisualIdentityAction } from "../../../events";
import { BottomBar } from "./bottomBar";
import type { IAdaptativeInGameView, IVisualIdentityDesign } from "../../../identity";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class InGameView extends LoadedView implements IAdaptativeInGameView {
	/**
	 * Bottom bar
	 */
	public bottomBar: BottomBar;
	/**
	 * Defines the settings component used to draw the settings window, buttons etc.
	 */
	public settingsComponent: SettingsComponent;
	/**
	 * Defines the quit popin used to confirm quitting the applet (from the editor)
	 */
	public quitPopin: QuitPopin;

	private _settingsEnabled: boolean = true;

	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();

		// Components
		this.bottomBar = new BottomBar(vi, this);
		this.settingsComponent = new SettingsComponent(vi);
		this.quitPopin = new QuitPopin(vi);
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		this.dockingBounds.width = Math.max(768, this.dockingBounds.width);

		// Resize
		this.vi.emitter.on("resize", () => this._resetBottomOverlay());

		// Quick button
		this.bottomBar.settingsButton.text.text = i18n.t("vi.settingsButton");
		this._initSettingsButtonEvents();

		// Actions
		this.vi.emitter.on(EVisualIdentityAction.SettingsEnabled, (enabled: boolean) => {
			this.bottomBar.settingsButton.enabled = this._settingsEnabled = enabled;
		});

		this.vi.emitter.on(EVisualIdentityAction.RulesEnabled, (enabled: boolean) => {
			this.settingsComponent.rulesEnabled = enabled;
		});

		this.vi.emitter.on(EVisualIdentityAction.UpdateDesign, (data) => {
			this._updateDesign(data || {});
		});

		this.vi.emitter.on(EVisualIdentityAction.AddControlButton, (data: Button[]) => {
			data.forEach((d) => this.bottomBar.addButton(d));
		});

		// Events
		this.vi.emitter.on(EVisualIdentityEvent.ConfirmQuitGame, () => {
			this.quitPopin.show();
		});
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		// Reset bottom overlay
		this._resetBottomOverlay();
	}

	/**
	 * Inits the Indstillinger button events such as:
	 * - pointertap
	 * - pointerover (animation)
	 * - pointerout (animation)
	 */
	private _initSettingsButtonEvents(): void {
		this.bottomBar.settingsButton.on("pointertap", () => {
			if (!this._settingsEnabled) {
				return;
			}

			// Listen close event
			this.settingsComponent.once(EVisualIdentityEvent.HideSettings, () => this._hideSettings());

			// Track and show
			tracker.track(new SettingsEvent(ESettingsEvent.Show));
			this.bottomBar.settingsButton.enabled = false;
			this.settingsComponent.show();

			// Send event
			this.vi.emitter.emit(EVisualIdentityEvent.ShowSettings);
		});

		this.quitPopin.on(EVisualIdentityEvent.HideSettings, () => this._hideSettings());
	}

	/**
	 * Hides the settings.
	 */
	private _hideSettings(): void {
		// Track and re-enable
		tracker.track(new SettingsEvent(ESettingsEvent.Hide));
		this.bottomBar.settingsButton.enabled = true;

		// Notify
		this.vi.emitter.emit(EVisualIdentityEvent.HideSettings);
	}

	/**
	 * Resets the bottom overlay by taking the game's dimensions
	 */
	private _resetBottomOverlay(): void {
		// Force updating bounds
		this.getBounds();

		// Get world transform
		const transform = new Transform();
		transform.setFromMatrix(this.worldTransform);

		this.bottomBar.onResize();
		/*
		this.bottomOverlay.clear();
		this.bottomOverlay.beginFill(0x0, 0.5);
		this.bottomOverlay.drawRect(0, 0, this.vi.game.renderer.width / transform.scale.x, 90);
		this.bottomOverlay.endFill();
		*/
	}

	/**
	 * Updates the design of the overall in-game-view
	 */
	private _updateDesign(data: IVisualIdentityDesign): void {
		data.buttonsStyles = data.buttonsStyles || ({} as any);
		data.titleStyles = data.titleStyles || ({} as any);
		data.contentStyles = data.contentStyles || ({} as any);

		// Settings component
		this.settingsComponent.background.texture = Texture.fromFrame(
			this.settingsComponent.background.texture.textureCacheIds[0]
		);
		this.settingsComponent.fill.texture = Texture.fromFrame(
			this.settingsComponent.fill.texture.textureCacheIds[0]
		);

		this.settingsComponent.muteButton.icon.texture = Texture.fromFrame(
			this.settingsComponent.muteButton.icon.texture.textureCacheIds[0]
		);
		this.settingsComponent.muteButton.button.texture = Texture.fromFrame(
			this.settingsComponent.muteButton.button.texture.textureCacheIds[0]
		);
		this.settingsComponent.unmuteButton.icon.texture = Texture.fromFrame(
			this.settingsComponent.unmuteButton.icon.texture.textureCacheIds[0]
		);
		this.settingsComponent.unmuteButton.button.texture = Texture.fromFrame(
			this.settingsComponent.unmuteButton.button.texture.textureCacheIds[0]
		);

		const settingsCross = this.settingsComponent.closeButton.getChildByName("cross") as Sprite;
		settingsCross.texture = Texture.fromFrame(settingsCross.texture.textureCacheIds[0]);

		this._updateTextDesign(this.settingsComponent.title, data.titleStyles);

		this._updateButtonDesign(this.settingsComponent.closeButton, data.buttonsStyles);
		this._updateButtonDesign(this.settingsComponent.instructionsButton, data.buttonsStyles);
		this._updateButtonDesign(this.settingsComponent.aboutButton, data.buttonsStyles);
		this._updateButtonDesign(this.settingsComponent.winningTableButton, data.buttonsStyles);
		this._updateButtonDesign(this.settingsComponent.rulesButton, data.buttonsStyles);
		this._updateButtonDesign(this.settingsComponent.quitButton, data.buttonsStyles);

		// Quit popin
		this.quitPopin.background.texture = Texture.fromFrame(
			this.quitPopin.background.texture.textureCacheIds[0]
		);
		this.quitPopin.fill.texture = Texture.fromFrame(this.quitPopin.fill.texture.textureCacheIds[0]);
		this._updateTextDesign(this.quitPopin.title, data.titleStyles);
		this._updateTextDesign(this.quitPopin.body, data.contentStyles);
		this._updateButtonDesign(this.quitPopin.confirmQuitButton, data.buttonsStyles);

		const quitCross = this.quitPopin.closeButton.getChildByName("cross") as Sprite;
		quitCross.texture = Texture.fromFrame(quitCross.texture.textureCacheIds[0]);

		this._updateButtonDesign(this.quitPopin.closeButton, data.buttonsStyles);
		this._updateButtonDesign(this.quitPopin.confirmQuitButton, data.buttonsStyles);
	}

	/**
	 * Updates the given's button
	 */
	private _updateButtonDesign(button: Button, styles: IStyles): void {
		if (button.idleTexture && button.idleTexture !== Texture.WHITE) {
			button.idleTexture = Texture.fromFrame(button.idleTexture.textureCacheIds[0]);
		}
		if (button.overTexture && button.overTexture !== Texture.WHITE) {
			button.overTexture = Texture.fromFrame(button.overTexture.textureCacheIds[0]);
		}
		if (button.clickedTexture && button.clickedTexture !== Texture.WHITE) {
			button.clickedTexture = Texture.fromFrame(button.clickedTexture.textureCacheIds[0]);
		}
		if (button.disabledTexture && button.disabledTexture !== Texture.WHITE) {
			button.disabledTexture = Texture.fromFrame(button.disabledTexture.textureCacheIds[0]);
		}

		this._updateTextDesign(button.text, styles);

		button.backgroundTexture = button.idleTexture;
	}

	/**
	 * Updates the given textfield's styles
	 */
	private _updateTextDesign(text: TextField, styles: IStyles): void {
		text.styles = Tools.assign(text.styles, styles);
	}
}
