import { LoadedView } from "@gdk/core-pixi";

export class IntroductionView extends LoadedView {
	/**
	 * Constructor
	 */
	public constructor() {
		super();
	}

	/**
	 * Animate the clip
	 * @param done called once the animation is done
	 */
	public animate(done: () => void): void {
		// DS wants to skip introduction, so we just done in order to
		// respect VI contract
		done();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Empty for now...
	}
}
